/**
*
* RD Navbar Thin
* 
*/
.rd-navbar-thin {
	// Static
	&.rd-navbar-static {
		background-color: transparent;
		font-weight: 500;

		.rd-navbar-main-outer {
			padding-left: $grid-gutter-width / 2;
			padding-right: $grid-gutter-width / 2;
		}

		.rd-navbar-main {
			position: relative;
			display: flex;
			align-items: center;
			justify-content: space-between;
			max-width: $rd-navbar-static-width;
			padding: 23px 0;
			margin-left: auto;
			margin-right: auto; 
		}

		.rd-navbar-nav-wrap {
			position: absolute;
			top: 0;
			bottom: 0;
			left: 200px;
			right: 80px;
			z-index: 102;
			display: flex;
			align-items: center;
			justify-content: flex-end;
			min-width: 690px;
			background: transparent;
			opacity: 0;
			visibility: hidden;
			transition: .44s ease-in-out;
			
			.rd-navbar-brand {
				display: none;
			}
			
			&.active {
				opacity: 1;
				visibility: visible;
			}
		}

		.rd-navbar-list {
			@include spacing(45px, 10px);

			> li {
				display: inline-block;
				vertical-align: middle;
				white-space: nowrap;

				> * {
					display: inline-block;
					vertical-align: middle;
					text-transform: uppercase;
				}

				.icon {
					font-size: 1.25em;

					&:before {
						vertical-align: middle;
					}
				}

				a[href*='tel:'] {
					font-size: 18px;
				}

				> * + * { margin-left: 8px; }
			}
		}
		
		.rd-navbar-panel {
			display: flex;
			align-items: center;
			justify-content: space-between;
			width: 100%;
			padding-right: 40px;
		}
		
		.ie-10 &,
		.ie-11 & {
			.rd-navbar-panel {
				justify-content: flex-start;
			}
			
			.rd-navbar-block {
				width: 100%;
				justify-content: flex-end;
			}
		}

		.rd-navbar-toggle {
			position: absolute;
			right: 0;
			top: 50%;
			display: block;
			transform: translate3d(0, -50%, 0);
		}

		.rd-navbar-block {
			display: flex;
			align-items: center;
			opacity: 1;
			visibility: visible;
			transition: .44s ease-in-out;

			&.active {
				opacity: 0;
				visibility: hidden;
			}
		}

		// RD Navbar Brand
		.rd-navbar-brand {
			max-width: 300px;
			img {
				width: auto;
				height: auto;
				max-width: 200px;
				max-height: 200px;
			}
		}

		.rd-nav-item.opened .rd-nav-link,
		.rd-nav-item.focus .rd-nav-link,
		.rd-nav-item.active .rd-nav-link,
		.rd-nav-item.opened .rd-navbar-submenu-toggle,
		.rd-nav-item.focus .rd-navbar-submenu-toggle,
		.rd-nav-item.active .rd-navbar-submenu-toggle,
		.rd-nav-link:hover,
		.rd-nav-link:hover ~ .rd-navbar-submenu-toggle,
		.rd-navbar-search-toggle:hover {
			color: lighten($primary, 20%);
		}

		.rd-menu {
			margin-top: 27px;
		}
		
		.rd-navbar-megamenu {
			margin-top: 24px;
			width: calc(100% + #{nth($rd-navbar-menu-padding, 2)} + 15px);
		}

		// RD Navbar Search
		.rd-navbar-search {
			position: static;
		}
		
		* + .rd-navbar-search-toggle {
			margin-left: 32px;
		}
		
		.rd-search {
			position: absolute;
			top: 0;
			bottom: 0;
			left: 0;
			right: 0;
			display: flex;
			width: calc(100% - 70px);
			margin: 0;

			&:before {
				content: '';
				position: absolute;
				left: 50%;
				top: 0;
				transform: translateX(-50%);
				width: 120vw;
				height: 100%;
				background-color: $gray-100;
			}
			
			.form-wrap {
				display: flex;
				width: 100%;
				
				&::before {
					content: '\f43b';
					position: absolute;
					top: 50%;
					transform: translate3d(0, -50%, 0);
					font-family: 'Material Design Icons';
					font-size: 30px;
					line-height: 1;
					//color: $white;
				}
			}
			
			.form-input,
			.form-label {
				padding-left: 40px;
				padding-right: 50px;
				font-size: 30px;

			}
			
			.form-input {
				background-color: transparent;
				border: 0;
				border-radius: 0;
			}

			.form-label {
				top: 50%;
				transform: translate3d(0, -50%, 0);
			}
		}

		.rd-search-results-live {
			height: 100vh;
			margin: 1px 0 0 0;
		}
		
		.search-list {
			display: flex;
			flex-wrap: wrap;
			margin-bottom: -20px;
		}
		
		.search-list-item {
			width: 25%;
			padding-left: 15px;
			padding-right: 15px;
			margin-bottom: 20px;
		}

		.search-list-item + .search-list-item { margin-top: 0; }
		
		.rd-navbar-search-toggle {
			position: relative;
			z-index: 12;
		}
		
		// RD Navbar Popup
		.rd-navbar-popup-toggle { }
		
		.list-inline-bordered {
			position: relative;
		}
		
		@include media-breakpoint-between(lg, lg) {
			.rd-nav-item + .rd-nav-item {
				margin-left: 30px;
			}
		}

		@include media-breakpoint-up(xl) {
			.rd-navbar-panel {
				padding-right: 46px;
			}
			
			.rd-navbar-nav-wrap {
				left: 300px;
				right: 70px;
			}
		}

		&.rd-navbar--is-stuck,
		&.rd-navbar--is-clone {
			background-color: $gray-100;
			box-shadow: $rd-navbar-shadow;

			.rd-menu {
				margin-top: 27px;
			}
		}
	}

	// Fixed 
	&.rd-navbar-fixed {
		// RD Search
		.rd-navbar-search {
			.form-input {
				border-width: 0 0 1px 0;
				border-radius: 0;
			}
		}
		
		.rd-search {
			
		}

		.rd-navbar-search-toggle {
			position: fixed;
			top: 5px;
			right: 8px;
			
			span {
				font-size: 20px;
			}
		}
		
		// RD Navbar Block
		.rd-navbar-collapse {
			position: fixed;
			top: $rd-navbar-fixed-height + 4px;
			right: 0;
			max-width: 320px;
			padding: 20px 10px;
			background-color: $gray-100;
			box-shadow: $rd-navbar-shadow;
			opacity: 0;
			visibility: hidden;
			transform: translateY(-15px);
			@extend %rd-navbar-transition;

			&.active {
				opacity: 1;
				visibility: visible;
				transform: translateY(0);
			}
		}

		.rd-navbar-list {
			@include spacing(24px, 10px);

			li {
				> * + * { margin-left: 5px; }
			}
		}

		// RD Navbar Brand
		.rd-navbar-brand {
			display: none;
		}
		
		.rd-navbar-nav-wrap {
			.rd-navbar-brand {
				display: inline-block;
				line-height: 0;
				font-size: 0; 
				margin: 30px 20px 20px;
			}
		}
		
		@media (min-width: 480px) {
			.rd-navbar-brand {
				display: block;
			}

			.rd-navbar-nav-wrap {
				.rd-navbar-brand {
					display: none;
				}
			}
		}
	}
}
